import React from "react";
import "./Jumbotron.css";


const Jumbotron = props => (

  <div className="jumbotron">
    <h1 className="text-center">
      <strong>
        <i className="fa fa-newspaper-o"></i> New York Times Search</strong>
    </h1>
  </div>

);

export default Jumbotron;


